<template>
  <div class="header">
    <button class="btn primary" @click="$emit('newGame')">Новая игра</button>
    <button :class="['btn', {'hiden': selectedColumn === null, 'primary': selectedColumn != null}]" @click="removeRound">Удалить кон</button>
    <button class="btn primary" @click="$emit('addRound')">Добавить кон</button>
  </div>
</template>

<script>
export default {
  props: {
    selectedColumn: {
      type: Number,
      required: false,
      default: null,
    }
  },
  emits: {
    "newGame": null,
    "addRound": null,
    "removeRound": null,
  },
  data() {
    return {

    }
  },
  mounted() {
    
  },
  methods: {
    removeRound() {
      if(this.selectedColumn === null) return
      this.$emit('removeRound')
    }
  }
}
</script>