<template>
    <div class="footer">
        <button :class="['btn', {'primary': activeWindow === windowsEnum.score}]" @click="selectActiveTab(windowsEnum.score)">Очки</button>
        <button :class="['btn', {'primary': activeWindow == windowsEnum.players}]" @click="selectActiveTab(windowsEnum.players)">Игроки</button>
        <button :class="['btn', {'primary': activeWindow === windowsEnum.settings}]" @click="selectActiveTab(windowsEnum.settings)">Настройки</button>
    </div>
</template>

<script>
export default {
    props: {
        activeWindow: {
            type: Number,
            required: true,
        },
        windowsEnum: {
            type: Object,
            required: true
        }
    },
    emits: {
        "selectActiveTab"(tab) {
            if(Number(tab) >= 0) {
                return true
            } else {
                console.log('no parametr "tab" for emit selectActiveTab')
            }
        }
    },
    methods: {
        selectActiveTab(tab) {
            this.$emit("selectActiveTab", tab)
        }
    },
}
</script>