<template>
<div class="content">
    <table>
        <thead>
            <tr>
                <th>Игрок</th>
                <th v-for="i in roundsCount" :key="i" @click="$emit('selectColumn', i)" :class="[{'selectedColumn': selectedColumn === i}]">№{{ i }}</th>
                <th>Итого</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(player, idx) in players" :key="idx">
                <td>{{ player.name }}</td>
                <td
                    v-for="(scoreValue, i) in player.score"
                    :key = i
                    :class="['scoreValue', {'selectedColumn': selectedColumn === i + 1}]"
                    ><input type="number" v-model="player.score[i]">
                </td>
                <td 
                    :class="isFinalScore(getSumOfScores(player)) ? 'winnerScore' : ''"
                >{{ getSumOfScores(player) }}</td>
            </tr>
        </tbody>
    </table>
</div>   
</template>

<script>
export default {
    

    props: {
        selectedColumn: {
            type: Number,
            required: false,
            default: null
        },
        players: {
            type: Object,
            required: true
        },
        roundsCount: {
            type: Number,
            required: true
        },
        finalScore: {
            type: Number,
            required: true
        }
    },
    emits: ['selectColumn'],
    
    data() {
        return {

        }
    },
    methods: {
        getSumOfScores(player) {
            return player.score.reduce((partialSum, a) => Number(partialSum) + Number(a), 0)
        },
        isFinalScore(sum) {
            if(this.finalScore <= sum) return true
            else return false
        },
    }
}
</script>