<template>
  <Header
    v-if="activeWindow === windowsEnum.score"
    :selectedColumn="selectedColumn"
    @newGame="newGame"
    @addRound="addRound"
    @removeRound="removeRound"
  />
  <Scores
    v-if="activeWindow === windowsEnum.score"
    :selectedColumn="selectedColumn"
    :roundsCount="roundsCount"
    :players="players"
    :finalScore="finalScore"
    @selectColumn="selectColumn"
  />
  <Players
    v-if="activeWindow === windowsEnum.players"
    :players="players"
    @deletePlayer="deletePlayer"
    @addPlayer="addPlayer"
  />
  <Settings
    v-if="activeWindow === windowsEnum.settings"
    :finalScore="finalScore"
    @changeFinalScore="changeFinalScore"
  />
  <Footer
    :activeWindow="activeWindow"
    :windowsEnum="windowsEnum"
    @selectActiveTab="selectActiveTab"
  />
</template>

<script>
import Header from './components/Header.vue';
import Scores from './components/Scores.vue';
import Players from './components/Players.vue';
import Settings from './components/Settings.vue';
import Footer from './components/Footer.vue';

export default {
  data() {
    return {
      activeWindow: 0,
      windowsEnum: {score: 0, players: 1, settings: 2},
      selectedColumn: null,
      roundsCount: 1,
      players: [
        {name: 'Леша', score: [0]},
      ],
      finalScore: 40,
    }
  },
  methods: {
    newGame() {
      this.players.forEach(player => player.score = [])
      this.roundsCount = 0
      this.selectedColumn = null
      this.addRound()
    },
    addRound() {
      this.players.forEach(player => player.score.push(0))
      this.roundsCount++
    },
    removeRound() {
      if (this.selectedColumn === null) return
      if (this.selectedColumn > this.roundsCount) return
      this.players.forEach(player => player.score.splice(this.selectedColumn - 1, 1))
      this.roundsCount--
      this.selectedColumn = null
    },
    selectActiveTab(tab) {
      this.activeWindow = tab
    },
    selectColumn(newIdx) {
      if(this.selectedColumn != newIdx) this.selectedColumn = newIdx
      else this.selectedColumn = null
    },
    deletePlayer(idx) {
      if(idx >= 0 && idx < this.players.length) {
        this.players.splice(idx, 1)
      }
    },
    addPlayer() {
      let score = []
      for(let i = 0; i < this.roundsCount; i++) score.push(0)
      this.players.push({
        name: `Игрок ${this.players.length + 1}`,
        score
      })
    },
    changeFinalScore(value) {
      this.finalScore = value
    }
  },
  components: {Header, Scores, Players, Settings, Footer}
}
</script>

