<template>
<div class="content">
    <div class="playersListRow" style="width: 100%">
        <p style="width: auto">Играть до</p>
        <input type="number" style="width: 20%; text-align: center; margin: 0;" :value="finalScore" @input="changeFinalScoreHandler"/>
        <p style="width: auto">очков</p>
    </div>
</div>   
</template>

<script>
export default {
    props: {
        finalScore: {
            type: Number,
            required: true,
        }
    },
    emits: ["changeFinalScore"],
    
    data() {
        return {

        }
    },
    methods: {
        changeFinalScoreHandler(event) {
            this.$emit('changeFinalScore', Number(event.target.value))
        }
    }
}
</script>